const state = {
  bookFile: datas.obj,
  // listDatas: enhanceSort(datas.results),
  listDatas: datas.results

}
const mutations = {
  UPDATE_BOOKFILE: (state, data) => {
    state.bookFile = data
  },

  LOAD_BOOKFILE: (state) => {
    const data = localStorage.getItem(tokenKey) //Cookies.get(tokenKey)
    if (data) {
      // console.log(data)
      state.bookFile = JSON.parse(data)
    }

  },
  SAVE_BOOKFILE: (state, data) => {
    // Cookies.set(tokenKey, data)
    localStorage.setItem(tokenKey, data)
  },
}


